<template>
	<!-- <div></div> -->
	<ul class="songList" ref="list">
		<li class="singer-zhida song-list-item" v-if="searchList.zhida.type==2">
			<img v-if="searchList.zhida.singermid" :src="searchList.zhida.singermid|musicSrc" >
			<h5>{{searchList.zhida.singername}}</h5>
			<p>
				<span>单曲: {{searchList.zhida.songnum}}</span>
				<span>专辑: {{searchList.zhida.albumnum}}</span>
			</p>
		
		</li>	
		<li class="singer-zhida song-list-item" v-else>
			<img v-if="searchList.zhida.albummid" :src="searchList.zhida.albummid|photoSrc" >
			<h5>{{searchList.zhida.albumname}}</h5>
			<p>
				{{searchList.zhida.singername}}
			</p>
			
		</li>


		<li v-for="item in searchList.list" class="song-list-item" :key="item.songmid">
			<i class="icon-bofang"></i>
			<h5>{{item.songname}}</h5>
			<p>{{item.singer|toString}}</p>
			
		</li>
		<keep-alive>
			<li class="song-list-item loading" v-if="!searchList.isAll">
			<i class="icon-jiazai"></i>
		</li>
		<li class="loaded song-list-item" v-else>已全部加载</li>
		</keep-alive>
	</ul>
</template>

<script>
export default{ 
	name:"search-list",
	created(){
		// this.$nextTick(()=>{console.log(this.searchList)})
	
	},
	filters:{
		musicSrc(val){		
		
			return "https://y.gtimg.cn/music/photo_new/T001R68x68M000"+val+".jpg?max_age=2592000"
		},
		toString(val){
			return val.join(" / ");
		},
		photoSrc(val){return "https://y.gtimg.cn/music/photo_new/T002R68x68M000"+val+".jpg?max_age=2592000"}

	},
	props:{

		searchList:{
			type:Object,
			default(){
				return{
					isAll:false,
			zhida:{
				albumid:"",
				albumnum:"",
				albummid:"",
				albumname:"",
				singername:"",
				singermid:"",
				songnum:"",
				type:0
			},
			list:[{songname:"",songmid:"",singer:[]},]
			}


		}
	}},
	data(){return {}}

}

</script>

<style lang="less" type="text/less">
@import "~common/less/variable";
@import "~common/less/mixin";

@keyframes ro{
	from{
		transform: rotateZ(0);
		
	}
	to{
		transform: rotateZ(360deg);
		
	}
}
.songList{
	background-color: @color-background-l;
	.song-list-item{
		position: relative;
		padding-left: 110px; 
		height: 119px;
		overflow: hidden;
		border-top: 1px solid @border-color-d;
		.icon-bofang{
			position: absolute;
			font-size: @font-size-large-x;top:36px;left:36px;
		}
		h5{
			margin:20px 0 10px;
			line-height: 32px;
			font-size: @font-size-medium-x;
			color:@color-text;
			.text-over-flow;
		}
		p{
			color:#808080;
			.text-over-flow;
			font-size: @font-size-small;
		}
	}
	.singer-zhida{
		img{
			position:absolute;
			left: 8px;
			top: 8px;
			width: 80px;
			height: 80px;
		}

	}
	.loading{
		text-align: center;
		padding:0;
		
		.icon-jiazai{
			font-size: 80px;
			line-height: 120px;
			margin:20px auto;
			position: absolute;
			left: 50%;
			margin-left:-40px;
			animation: ro .5s linear infinite both;

		}
	}
	.loaded{
		text-align: center;
		line-height: 110px;
	}
}
</style>


