<template>
	<section class="search" ref="search" @scroll="scroll">
		<div class="search-form" >
			<div class="search-input">
				<i class="icon-sousuo"></i>
				<input @keyup.enter="searching" placeholder="搜索歌曲，歌单，专辑" type="text" v-model="search" ref="input" @focus="changeFocus(true)" class="search-input-text" :class="{focus:onfocus}">
				<i class="icon-empty" v-show="search" @click="empty"></i>
			</div>
			<span class="delete" v-show="onfocus" @click="cancle">取消</span>
		</div>
		<keep-alive>
			<hot-search @copyHot="copyHot" v-if="!onfocus" :special="special" :hotkey="hotkey"></hot-search>
			<history @delete="deleteHistory" v-else-if="isHistory" :history="histories"></history>
			<search-list v-else :searchList="searchList"></search-list  ref="mySearchList">
		</keep-alive>
		
	</section>
</template>

<script>
import HotSearch from "components/search/Hot-search"
import {ERR_OK} from 'api/config.js'
import {getHotKey,getSeachList} from "api/search"
import History from "components/search/History"
import SearchList from "components/search/searchList"
import {setIndex} from "common/js/array"

export default {
	name: 'search',
	components:{HotSearch,History,SearchList},
	data(){
		return{
			on:true,
			search:"",
			onfocus:false,
			special:{key:"",url:""},
			hotkey:[],
			histories:[],
			isHistory:true,
			p:1,
			songList:[],
			searchList:{
				isAll:false,
				zhida:{},
				list:{}
			}
		}
	},
	created(){
		this._getHotKey();
		
	},
	methods:{
		copyHot(val){
			this.search=val;
			this.$refs.input.focus();
		},
		scroll(){
			
			if(this.on){
				let val=this.$refs.search.scrollHeight-this.$refs.search.scrollTop<=this.$refs.search.clientHeight;
				if(val){
				this._getSearchList();
				this.on=false;
				let timeLimit=setTimeout(()=>{this.on=true;clearTimeout(timeLimit)},3000)
			}
			
			
		}},
		deleteHistory(index){
			this.histories.splice(index,1);
		},
		delete(){},
		empty(){this.search=""},
		cancle(){
			this.changeFocus(false);
			this.isHistory=true;
			this.search="";
			this.searchList={
				isAll:false,
				zhida:{},
				list:{}
			};
		},
		_getSearchList(){
			getSeachList(this.search,this.p).then(res=>{
				if(res.code===ERR_OK){
					let zhida=res.data.zhida,
					song=res.data.song,
					listAry=song.list,
					isAll=song.totalnum-(song.curnum*song.curpage)>0?false:true;
					let list=new Array;

					listAry.forEach(item=>{
						let obj=new Object;
						obj.songname=item.songname;
						obj.songmid=item.songmid;
						obj.singer=new Array;
						item.singer.forEach(item=>{
							obj.singer.push(item.name);
						})
						list.push(obj);

					})
				
					if(this.searchList.list.length){

						this.songList=this.songList.concat(list);
						
						this.searchList=Object.assign({},this.searchList,{zhida,isAll,list:this.songList})
					
					}else{

                         this.songList=list;
						this.searchList=Object.assign({},this.searchList,{zhida,isAll,list:this.songList})
					
					
					}
				
this.p++;
					
			


				}

			});
		},
		searching(){
			if(this.search!=""){
				setIndex(this.histories,this.search,0)
				this.songList=[];
				this.searchList={
				isAll:false,
				zhida:{},
				list:{}
			};
				this.isHistory=false;
				this.p=1;
				this._getSearchList();


			}	
		},
		_getHotKey(){
			getHotKey().then((res=>{
				if(res.code===ERR_OK){
					let key=res.data.special_key,url=res.data.special_url;
					this.special={key,url};

					this.hotkey=(res.data.hotkey.sort(()=>Math.random-0.5).splice(0,6));
				}
			}))
		},
		changeFocus(val){
			this.onfocus=val;
		},
	}
}
</script>

<style lang="less" type="text/less" scoped>
@import '~common/less/variable';
.search{
	background-color: @color-background-l;
	position: absolute;
	// margin-bottom:-2000px;
	// padding-bottom:2000px;
	left: 0;
	right: 0;
	bottom:0;
	top:200px;
	overflow-y: scroll;

}
.search-form{
	padding:20px;
	display:-webkit-box;
	background-color: @color-background;
	.search-input{
		position: relative;
		-webkit-box-flex: 1;

		.search-input-text{
			border-radius: 6px;background-color: @color-background-l;
			height:40px;
			border:none;
			padding: 0 24px 0 70px;
			width:100%;
			box-sizing: border-box;
			line-height: 72px;
			height:72px;
			font-size: @font-size-medium;
		}

	}
	
	.icon-sousuo{
		position: absolute;
		top:19px;
		left:19px;
		font-size: @font-size-large;
	}
	.icon-empty{
		position: absolute;
		font-size: @font-size-large;
		top:18px;right:24px;
	}
	.delete{

		line-height: 72px;
		padding: 0 20px;
		font-size: @font-size-medium;
		
	}

}
.search-on{

}

</style>



