import Vue from 'vue'
import Router from 'vue-router'
import Come from 'components/come'
import Home from 'components/home'
import Rank from 'components/rank'
import Recommend from 'components/recommend'
import Search from 'components/search'
import List from 'components/list'
import Singer from 'components/singer'
import ServerAgrement from 'components/tool/ServerAgrement'
import Uninstall from 'components/tool/Uninstall'
import fastclick from 'fastclick'
import VueLazyload from 'vue-lazyload'
// import Axios from 'axios'


fastclick.attach(document.body)

Vue.use(Router)
Vue.use(VueLazyload,{loading:require('common/img/lazy.png'),preLoad:1})
  // {preLoad:1.3,
  //   error:require('common/img/lazy.png'),
  //   loading:require('common/img/lazy.png'),
  //   attempt:1}
    // )
// Vue.prototype.$http = Axios



export default new Router({
  linkActiveClass: 'active',
  routes: [{
      path: '/',
      redirect: '/come'
    },
    {
      path: '/home',
      component: Home,
      children: [{
          path: '/',
          component: Recommend,
        },
        {
          path: '/recommend',
          component: Recommend
        },
        {
          path: '/rank',
          component: Rank
        },
        {
          path: '/search',
          component: Search
        }
      ]
    },
    {
      path: '/come',
      component: Come,
      children: [{
          path: '/',
          component: Recommend
        },
        {
          path: '/recommend',
          component: Recommend
        },
        {
          path: '/rank',
          component: Rank
        },
        {
          path: '/search',
          component: Search
        }
      ]
    },
    {
      path: '/server',
      component: ServerAgrement
    },
    {
      path: '/uninstall',
      component: Uninstall
    },
  ]
})



